import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import moment from 'moment';
import ProgressiveImage from 'react-progressive-image'

import Layout from '../components/layout'
import SEO from '../components/seo'
import Title from '../components/Title'
import PostMeta from '../components/postMeta'
import Excerpt from '../components/excerpt'
import HeaderImagePlaceFolder from '../components/headerImagePlaceHolder'

function betterDate(date) {
  return moment(date).format('YYYY-MM-DD');
}

const IndexPage = () => (
  <StaticQuery
    query={graphql`
    query {
      allMarkdownRemark(
        sort: { order: DESC, fields: [frontmatter___date] }
        limit: 1000
      ) {
        edges {
          node {
            excerpt(pruneLength: 160, truncate: true)
            frontmatter {
              title
              date
              path
              permalink
            }
          }
        }
      }
    }
    `}
    render={data => (
      <Layout
        headerImage={
          <ProgressiveImage src="/images/homepage.jpg" placeholder="">
            {(src, loading) => {
              return loading ? <HeaderImagePlaceFolder /> : (
                <img
                  src={src}
                  alt=""
                  style={{
                    margin: '0px',
                  }}
                />
              );
            }}
          </ProgressiveImage>
        }
      >
        <SEO
          title="首页"
          keywords={[
            `blog`,
            `technology`,
            `photography`,
          ]}
        />
        {
          data.allMarkdownRemark.edges.map(({ node }) => {
            let path = node.frontmatter.path;
            if (!path) {
              const date = new Date(node.frontmatter.date);
              const year = date.getFullYear().toString();
              let month = (date.getMonth() + 1).toString();
              if (month.length < 2) {
                month = '0' + month;
              }
              path = `/${year}/${month}/${node.frontmatter.permalink}`;
            }
            return (
              <div key={node.frontmatter.title}>
                <Title to={path}>
                  {node.frontmatter.title}
                </Title>
                <PostMeta>
                  Posted at {betterDate(node.frontmatter.date)}
                </PostMeta>
                <Excerpt>
                  {node.excerpt}
                </Excerpt>
              </div>
            )
          })
        }
      </Layout>
    )}
  />
)

export default IndexPage
